
// images
import bg_logo from '../../assets/bg_main_logo_img/logo__bg.mp4'

// styli
import "./main.scss"

function Main() {
    return(
        <div className="main__bg" >
            <video src={bg_logo} type='video/mp4' preload='auto' muted={false} autoPlay loop></video>
        </div>
    )
}

export default Main;