
import React, {lazy, Suspense} from "react";
// import { useEffect } from "react";

// bibliotek
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

// база данных
import freightObjDat from "../../assets/freightObj.js";
// style
import "./home.scss";
// conponets
// import CustomCursor from "../../components/CustomCursor";
import Main from "../../components/main/Main";
// import Portfolio from "../../components/portfolio/Portfolio";
import Awards from "../../components/awards/Awards";
import About from "../../components/about/about";
// import GridAnimation from "../../components/grid_animation/GridAnimation";
// import Work from "../../components/work/Work";
// import ScrollGorizont from "../../components/scrollGorizont/ScrollGorizont.jsx";
// import Freight from "../../components/freight/Freight.jsx";
import Spiner from "../../components/preloader/Spiner.jsx";
import ScrollGorizontTwo from "../../components/scrollGorizont/ScrollGorizontTwo.jsx";
// import HorizontalScroll from "../../components/HorizontalScroll/HorizontalScroll.jsx";



const ScrollGorizont = lazy(()=> import('../../components/scrollGorizont/ScrollGorizont.jsx'));
// const ScrollGorizontTwo = lazy(()=> import('../../components/scrollGorizont/ScrollGorizontTwo.jsx'));
const Portfolio = lazy(()=> import('../../components/portfolio/Portfolio.jsx'));
// const About = lazy(()=> import('../../components/about/about.jsx'));
const GridAnimation = lazy(()=> import('../../components/grid_animation/GridAnimation.jsx'));
const Work = lazy(()=> import('../../components/work/Work.jsx'));
const Freight = lazy(()=> import('../../components/freight/Freight.jsx'));

gsap.registerPlugin(ScrollTrigger);

const Home = ()=>{

    return(
            <div className="main__container">
                <Main/>
              <Suspense fallback={<Spiner/>}>
                <Portfolio/>
              </Suspense>
              <Suspense fallback={<Spiner/>}>
                <Work/>
              </Suspense>
                            
              <section className="scroll__container">
                <Suspense fallback={<Spiner/>}>
                  <section className="scrollGorizont__container"><ScrollGorizont /></section>
                </Suspense>
              </section>

              <ScrollGorizontTwo/>

              <About/>

              
              {/* <HorizontalScroll/> */}

              <Suspense fallback={<Spiner/>}>
                <GridAnimation/>
              </Suspense>

              <Suspense fallback={<Spiner/>}>
                <Freight freightObjDat={freightObjDat}/>
              </Suspense>
                <Awards/>
            </div>
    )
}
export default Home;